const state = {
  priceRange: null,
  storageKey: null
}

const getters = {
  priceRange: (state) => {
    return state.priceRange
  }
}

const actions = {
  init({ state }) {
    let userStorage = localStorage.getItem('user') || null
    if (userStorage) {
      userStorage = JSON.parse(userStorage)
      state.storageKey = `site-choosed-barbershop-price-range-${userStorage.id}`
      state.priceRange = localStorage.getItem(state.storageKey) ? JSON.parse(localStorage.getItem(state.storageKey)) : null
    }
  },
  setPriceRange({ commit, state }, priceRange) {
    commit('setPriceRange', priceRange)
    localStorage.setItem(state.storageKey, JSON.stringify(priceRange))
  }
}

const mutations = {
  setPriceRange(state, priceRange) {
    state.priceRange = priceRange
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
