const state = {
  city: null,
  storageKey: null
}

// getters
const getters = {
  city: (state) => {
    return state.city
  }
}

// actions
const actions = {
  init({ state }) {
    let userStorage = localStorage.getItem('user') || null
    if (userStorage) {
      userStorage = JSON.parse(userStorage)
      state.storageKey = `site-choosed-barbershop-city-${userStorage.id}`
      state.city = localStorage.getItem(state.storageKey) || null
    }
  },
  setCity({ commit, state }, city) {
    commit('setCity', city)
    localStorage.setItem(state.storageKey, city)
  }
}

// mutations
const mutations = {
  setCity(state, city) {
    state.city = city
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
