const Admin = () => import('@/components/admin/Admin')
const Barbershop = () => import('@/pages/admin/Barbershop')
const Barber = () => import('@/pages/admin/Barber')
const Plans = () => import('@/pages/admin/Plans')
const Profile = () => import('@/pages/admin/Profile')
const Service = () => import('@/pages/admin/Service')
const Dashboard = () => import('@/pages/admin/Dashboard')
const DashboardSchedules = () => import('@/components/admin/dashboard/DashboardSchedules')
const DashboardSchedulingRequests = () => import('@/components/admin/dashboard/DashboardSchedulingRequests')
const DashboardRevenues = () => import('@/components/admin/dashboard/DashboardRevenues')

const admin = [
  {
    path: '/admin',
    component: Admin,
    children: [
      {
        path: '',
        name: 'admin',
        redirect: { name: 'admin-dashboard' }
      },
      {
        path: 'dashboard',
        component: Dashboard,
        children: [
          {
            path: '',
            name: 'admin-dashboard',
            redirect: { name: 'admin-schedules' }
          },
          {
            path: 'agendamentos',
            component: DashboardSchedules,
            name: 'admin-schedules',
            meta: {
              requiresAuth: true,
              isAdmin: true,
              requiresAdminProfile: true,
              pageName: 'Agendamentos',
              alias: 'dashboard',
              bottomBar: 'schedules',
              css: 'pa-0',
              tab: 0
            }
          },
          {
            path: 'solicitacoes',
            component: DashboardSchedulingRequests,
            name: 'admin-scheduling-requests',
            meta: {
              requiresAuth: true,
              isAdmin: true,
              requiresAdminProfile: true,
              pageName: 'Solicitações',
              alias: 'dashboard',
              bottomBar: 'scheduling_requests',
              css: 'pa-0',
              tab: 1,
              noChooseDate: true
            }
          },
          {
            path: 'faturamento',
            component: DashboardRevenues,
            name: 'admin-revenues',
            meta: {
              requiresAuth: true,
              isAdmin: true,
              requiresAdminProfile: true,
              pageName: 'Faturamento',
              alias: 'dashboard',
              bottomBar: 'revenues',
              css: 'pa-0',
              tab: 2
            }
          }
        ]
      },
      {
        path: 'minha-barbearia',
        component: Barbershop,
        name: 'admin-barbershop',
        meta: {
          requiresAuth: true,
          requiresAdmin: true,
          isAdmin: true,
          pageName: 'Minha Barbearia',
          alias: 'barbershop',
          css: 'pa-0 mb-8'
        }
      },
      {
        path: 'barbeiros',
        component: Barber,
        name: 'admin-barber',
        meta: {
          requiresAuth: true,
          requiresAdmin: true,
          isAdmin: true,
          pageName: 'Barbeiros',
          alias: 'barber',
          breadcrumb: [{ text: 'Dashboard', disabled: true, href: "/admin/barber" }]
        }
      },
      {
        path: 'meus-servicos',
        component: Service,
        name: 'admin-service',
        meta: {
          requiresAuth: true,
          requiresAdmin: true,
          isAdmin: true,
          pageName: 'Preços e Serviços',
          alias: 'service',
          css: 'services'
        }
      },
      {
        path: 'planos',
        component: Plans,
        name: 'admin-plans',
        meta: {
          requiresAuth: true,
          requiresAdmin: true,
          isAdmin: true,
          pageName: 'Planos',
          alias: 'plans',
          breadcrumb: [
            { text: "Dashboard", disabled: false, href: "/admin/dashboard" },
            { text: "Planos", disabled: true }
          ]
        }
      },
      {
        path: 'meu-perfil',
        component: Profile,
        name: 'admin-profile',
        meta: {
          requiresAuth: true,
          isAdmin: true,
          pageName: 'Meu Perfil',
          alias: 'profile',
          css: 'pa-0',
          breadcrumb: [
            { text: "Dashboard", disabled: false, href: "/admin/dashboard" },
            { text: "Planos", disabled: true }
          ]
        }
      }
    ]
  }
]

export default admin