const state = {
  date: null
}

const getters = {
  date: (state) => {
    return state.date
  }
}

const actions = {
  setDate({ commit }, date) {
    commit('setDate', date)
  }
}

const mutations = {
  setDate(state, date) {
    state.date = date
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
