const state = {
  qtdRequestsPending: 0
}

const qtdPendingKey = 'qtd-pending'

const getters = {
  qtdRequestsPending: (state) => {
    return state.qtdRequestsPending
  }
}

const actions = {
  init({ commit }) {
    let barberStorage = localStorage.getItem('barber')
    if (barberStorage) {
      barberStorage = JSON.parse(barberStorage)
      const qtd = localStorage[`${qtdPendingKey}-${barberStorage.id}`] || 0
      commit('setQtdRequestsPending', qtd)
    }
  },
  setQtdRequestsPending({ commit }, qtd) {
    let barberStorage = localStorage.getItem('barber')
    if (barberStorage) {
      barberStorage = JSON.parse(barberStorage)
      localStorage.setItem(`${qtdPendingKey}-${barberStorage.id}`, qtd)
      commit('setQtdRequestsPending', qtd)
    }
  }
}

const mutations = {
  setQtdRequestsPending(state, qtd) {
    state.qtdRequestsPending = qtd
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
