import axios from 'axios';

//const base = 'http://localhost:8000/api'
const base = 'https://api-appmeubarbeiro-com-br.umbler.net/public/api'

const Axios = axios.create({
  baseURL: base,
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: false
});

Axios.interceptors.request.use(
  function (config) {
    const token = localStorage.token;
    const userToken = localStorage.userToken;
    if (token) config.headers.Token = token;
    else if (userToken) config.headers.Token = userToken;
    config.headers.moderatortoken = localStorage.moderatorToken || null
    return config;
  },
  function (error) {
    return Promise.reject(error);
  }
);

Axios.interceptors.response.use(
  function (response) {
    return response;
  },
  function (error) {
    if (401 === error.response.status) {
      const path = window.location.pathname
      //localStorage.lastUrl = path
      //path = path.replace('/', '');
      //localStorage.routePath = path;
      let destiny = '/autenticacao/login'
      if (path.includes('admin')) destiny = '/barbeiro/autenticacao'
      else if (path.includes('moderadores')) destiny = '/moderadores/login'
      window.location.href = destiny;
    } else return Promise.reject(error);
  }
);

export default Axios;
