import Vue from 'vue'
import Vuex from 'vuex'
import AdminSchedulesStore from './modules/AdminSchedulesStore'
import BarberStore from './modules/BarberStore'
import BarbershopStore from './modules/BarbershopStore'
import ChoosedBarbershopCityStore from './modules/ChoosedBarbershopCityStore'
import ChoosedBarbershopDateStore from './modules/ChoosedBarbershopDateStore'
import ChoosedBarbershopPriceRangeStore from './modules/ChoosedBarbershopPriceRangeStore'
import ChoosedServiceTypesStore from './modules/ChoosedServiceTypesStore'
import DashboardStore from './modules/DashboardStore'
import SnackbarStore from './modules/SnackbarStore'
import LoaderStore from './modules/LoaderStore'
import MenuStore from './modules/MenuStore'
import NotificationStore from './modules/NotificationStore'
import TermPromptStore from './modules/TermPromptStore'
import UserStore from './modules/UserStore'

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    AdminSchedulesStore,
    BarberStore,
    BarbershopStore,
    ChoosedBarbershopCityStore,
    ChoosedBarbershopDateStore,
    ChoosedBarbershopPriceRangeStore,
    ChoosedServiceTypesStore,
    DashboardStore,
    LoaderStore,
    MenuStore,
    NotificationStore,
    SnackbarStore,
    TermPromptStore,
    UserStore,
  },
});
