const state = {
  choosedSeviceTypes: [],
  storageKey: null
}

// getters
const getters = {
  choosedSeviceTypesState: (state) => {
    return state.choosedSeviceTypes
  }
}

// actions
const actions = {
  init({ state }) {
    let userStorage = localStorage.getItem('user') || null
    if (userStorage) {
      userStorage = JSON.parse(userStorage)
      state.storageKey = `site-choosed-service-types-${userStorage.id}`
      state.choosedSeviceTypes = localStorage.getItem(state.storageKey) ? JSON.parse(localStorage.getItem(state.storageKey)) : []
    }
  },
  setChoosedSeviceTypes({ commit, state }, choosedSeviceTypes) {
    commit('setChoosedSeviceTypes', choosedSeviceTypes)
    localStorage.setItem(state.storageKey, JSON.stringify(choosedSeviceTypes))
  }
}

// mutations
const mutations = {
  setChoosedSeviceTypes(state, choosedSeviceTypes) {
    state.choosedSeviceTypes = choosedSeviceTypes
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
