const BarbershopDetail = () => import('@/pages/site/BarbershopDetail');
const Base = () => import('@/pages/site/Base');
const ChangePassword = () => import('@/pages/site/ChangePassword');
const Index = () => import('@/pages/site/Index')
const Offline = () => import('@/pages/site/Offline');
const PrivacyPolicy = () => import('@/pages/site/PrivacyPolicy');
const Profile = () => import('@/pages/site/Profile');
const Schedule = () => import('@/pages/site/Schedule');
const ScheduleDetail = () => import('@/pages/site/ScheduleDetail');
const Search = () => import('@/pages/site/Search')
const SiteTemplate = () => import('@/pages/site/SiteTemplate')
const Terms = () => import('@/pages/site/Terms');

const site = [
  {
    path: '/',
    component: Index,
    name: 'index',
    meta: { isAdmin: false, navigationBarLink: 'search' },
  },
  {
    path: '',
    component: SiteTemplate,
    children: [
      {
        path: 'pesquisa',
        component: Search,
        name: 'search',
        meta: { isAdmin: false, pageName: 'Barbearias', navigationBarLink: 'search' },
      },
      {
        path: 'barbearia/:id/:url',
        name: 'site-barbershop',
        component: BarbershopDetail,
        meta: {
          pageName: 'Detalhes da Barbearia',
          navigationBarLink: 'search',
          rightTopBar: [
            { title: 'Compartilhar', action: 'share', icon: 'share' }
          ]
        }
      }
    ]
  },
  {
    path: '',
    component: Base,
    children: [
      {
        path: 'alterar-senha',
        name: 'site-change-password',
        component: ChangePassword,
        meta: {
          isAdmin: false,
          fluid: true,
          noPadding: true,
          backButton: true,
          backButtonRouteName: 'search',
        },
      },
      {
        path: '/barbearias-favoritas',
        component: Search,
        name: 'favorits',
        meta: { isAdmin: false },
      },
      {
        path: 'meus-agendamentos',
        component: Schedule,
        name: 'site-schedule',
        meta: {
          requiresAuth: true,
          isAdmin: false,
          menuButton: true,
          alias: 'schedule',
          pageTitle: 'Meus agendamentos',
        },
      },
      {
        path: 'agendamento/:id',
        component: ScheduleDetail,
        name: 'site-schedule-detail',
        meta: {
          requiresAuth: true,
          isAdmin: false,
          backButton: true,
          alias: 'schedule',
          pageTitle: 'Agendamento',
        },
      },
      {
        path: 'offline',
        name: 'offline',
        component: Offline,
        meta: { isAdmin: false },
      },
      {
        path: '/politica-de-privacidade',
        name: 'site-privacy-policy',
        component: PrivacyPolicy,
        meta: {
          backButton: true,
          backButtonRouteName: 'search',
          isAdmin: false,
          pageTitle: 'Política de Privacidade',
          pageCssClass: 'terms',
        },
      },
      {
        path: '/meu-perfil',
        name: 'site-profile',
        component: Profile,
        meta: {
          requiresAuth: true,
          backButton: true,
          fluid: true,
          noPadding: true,
          backButtonRouteName: 'search',
          isAdmin: false,
          pageTitle: 'Meu Perfil',
          pageCssClass: 'profile',
        },
      },
      {
        path: '/termo-de-uso',
        name: 'site-terms',
        component: Terms,
        meta: {
          backButton: true,
          backButtonRouteName: 'search',
          isAdmin: false,
          pageTitle: 'Termo de Uso',
          pageCssClass: 'terms',
        },
      },
    ],
  },
];

export default site;
