const state = {
  period: null,
  barber: null
}

const barberKey = 'admin-dashboard-barber'
const periodKey = 'admin-dashboard-period'

const getters = {
  barber: state => {
    return state.barber
  },
  period: state => {
    return state.period
  }
}

const actions = {
  init({ state }) {
    state.barber = null
    let barberStorage = localStorage.getItem('barber')
    if (barberStorage) {
      barberStorage = JSON.parse(barberStorage)
      const barber = localStorage[`${barberKey}-${barberStorage.id}`]
      state.barber = barber ? JSON.parse(barber) : null
      const period = localStorage[`${periodKey}-${barberStorage.id}`]
      state.period = period ? JSON.parse(period) : null
    }
  },
  setBarber({ commit }, barber) {
    let barberStorage = localStorage.getItem('barber')
    if (barberStorage) {
      barberStorage = JSON.parse(barberStorage)
      localStorage[`${barberKey}-${barberStorage.id}`] = JSON.stringify(barber)
      commit('setBarber', barber)
    }
  },
  setPeriod({ commit }, period) {
    let barber = localStorage.getItem('barber')
    if (barber) {
      barber = JSON.parse(barber)
      localStorage[`${periodKey}-${barber.id}`] = JSON.stringify(period)
      commit('setPeriod', period)
    }
  }
}

const mutations = {
  setBarber(state, barber) {
    state.barber = barber
  },
  setPeriod(state, period) {
    state.period = period
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}