import Vue from 'vue';
import './plugins/axios';
import App from './App.vue';
import Axios from './plugins/axios';
import barberStatus from './constants/BarberStatus';
import router from './routes';
import vuetify from './plugins/vuetify';
import store from './store';
import VueCarousel from 'vue-carousel';
import VueTheMask from 'vue-the-mask';
import VueAnalytics from 'vue-analytics';
//import './registerServiceWorker'

Vue.use(VueCarousel)
Vue.use(VueTheMask)

const prod = true

Vue.use(VueAnalytics, {
  id: 'UA-96032364-2',
  router,
  debug: {
    enabled: false,
    sendHitTask: prod
  }
});

const urlApiLocal = 'http://localhost:8000/api'
const urlBackendLocal = 'http://localhost:8000'
const urlApiProd = 'https://api-appmeubarbeiro-com-br.umbler.net/public/api'
const urlBackendProd = 'https://api-appmeubarbeiro-com-br.umbler.net'

Vue.prototype.$axios = Axios;
Vue.prototype.$url_backend = prod ? urlBackendProd : urlBackendLocal
Vue.prototype.$url_api = prod ? urlApiProd : urlApiLocal

Vue.prototype.$SCHEDULE_STATUS = {
  APROVADO: 1,
  CANCELADO: 2,
  AGUARDANDO: 3,
  REPROVADO: 4,
};

Vue.prototype.$BARBER_STATUS = barberStatus

Vue.config.productionTip = false;

new Vue({
  store,
  router,
  vuetify,
  render: (h) => h(App),
}).$mount('#app');
